<script>
export default {
  data() {
    return {
      name: "马诗诗",
      address: "西宁市",
      checked: false,
      num: 0,
    };
  },
  mounted() {
    this.checked = localStorage.getItem("checked")
      ? JSON.parse(localStorage.getItem("checked"))
      : false;
  },
  // watch: {
  //   checked: {
  //     handler(newV) {
